$color-main: #0056A9;
$color-red: #C23349;
$color-green: #5CAB5F;
$color-dark-blue: #32449F;
$color-white: #ddd;


.first-section {
    min-height: 766px;
    background: $color-main;
    &__content {
        max-width: 1140px;
        margin: 0 auto;
        display: flex;
        flex-direction: column; }
    &__title {
        font-size: 55px;
        color: white;
        text-align: center;
        margin-top: 35px;
        margin-bottom: 35px; } }

.second-section {
    min-height: 480px;
    background: $color-main;
    &__cards-box {
        max-width: 1140px;
        margin: 0 auto;
        display: flex;
        flex-direction: row;
        margin-bottom: 80px; } }

.cards-item {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 360px;
    width: 360px;
    border-radius: 20px;
    margin-right: 30px;
    border: 2px solid $color-white;
    box-shadow: 8px 4px 4px 0px rgba(0, 0, 0, 0.25);
    &:last-child {
        margin-right: 0; }
    &__bolts-box {
        width: 322px;
        height: 322px; }
    &__bolts_y {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        margin-bottom: 289px; }
    &__content {
        width: 290px;
        position: absolute;
        top: 30px;
        text-align: center; }
    &__title {
        font-size: 32px;
        margin-bottom: 15px; }
    &__description {
        font-size: 22px;
        text-align: left; } }

.cards-item_red {
    background: $color-red; }

.cards-item_green {
    background: #656565;
 }    // background: $color-green

.cards-item_blue {
    background: $color-dark-blue; }

.services-section {
    position: relative;
    min-height: 1120px;
    background: $color-main;
    &__image {
        position: relative;
        height: 1000px;
        margin: 0 auto;
        background: url("/static/carwash/images/car-service.png"), $color-main 50%;
        background-repeat: no-repeat;
        background-blend-mode: screen;
        margin-bottom: 60px; }
    &__price {
        position: absolute;
        top: 119px;
        left: 370px;
        min-height: 755px;
        width: 1140px;
        border-radius: 16px;
        border: 3px solid $color-white;
        box-shadow: 8px 5px 4px 0px rgba(0, 0, 0, 0.25);
        background: #656565;
 }        // background: $color-green
    &__link-freepik {
        position: absolute;
        color: #3304dd;
        text-align: right;
        margin-right: 20px;
        top: 965px;
        right: 18px; }
    &__th1 {
        font-size: 32px; }
    &__description {
        text-align: left; } }

.third-section {
    min-height: 600px;
    background: $color-main; }

table, td, th {
    margin: 0 auto;
    color: white; }

table {
    margin-top: 10px; }


th {
    margin: 0;
    padding: 27px;
    font-size: 18px;
    border-bottom: 1px solid #ddd;
    vertical-align: bottom; }
td {
    padding: 9px;
    font-size: 16px;
    text-align: center;
    border-bottom: 1px solid #ddd; }

tr:nth-child(even) {
    background-color: #474747;
 }    // background-color: #22843e



